import React, {Component, useState, useRef} from 'react';
import {Button, Col, Row, Navbar, Nav, ButtonGroup, Modal, Container, Form} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { motion } from "framer-motion"
import Snowfall from 'react-snowfall'


function LoadingScreen(){
	const [settingModal, setSettingModal] = useState(false);
	const suggestions = useRef(false);
	const grammar = useRef(false);
	const wordCount = useRef(false);
	const changeModal = () => setSettingModal(!settingModal); 
	const closeModal = () => setSettingModal(false);

	class SettingModal extends Component {
		constructor(props) {
			super(props);
		}

	 render() {
	 	return (<div id="loadingModal">
	 		<Modal show={settingModal} onHide={closeModal}>
   		  		<Modal.Header>
   		  			<Modal.Title>
   		  				Credits:
   		  				
   		  			</Modal.Title>
   		  		</Modal.Header>
   		  		<Modal.Body>
   		  			<strong>Collaborators: </strong>
   		  			<div id="surrounding">
   		  				<i><span>Ethan Schauder</span>
   		  				<span>Ethan Edwards</span>
   		  				<span>Collin Steele</span>
   		  				<span>Easton Snow</span></i>
   		  				</div>
   		  			</Modal.Body>
   		  			<Modal.Footer>
   		  			<p>Thank you for helping with TSA!</p>
   		  			</Modal.Footer>
   		 </Modal>

	 		</div>

	 		)
	 }
	}

	class ModalButton extends Component {
    constructor(props) {
      super(props);
    }
    render() {
      return (
      <div id="settings">
      	<motion.div initial={{ opacity: 0, y:-50}} animate={{ opacity: 1, y: 0}} transition={{duration: 0.5, delay: 1}} viewport={{ once: true }}>
   		  <Button variant="outline-secondary" size="lg" onClick={changeModal}>Credits</Button>
   		</motion.div>
   		</div>
      );
    }
  }

	class HeaderApp extends Component {
    constructor(props) {
      super(props);
    }
    render() {
      return (
      <div id="Header">
    	<head>
      		<style type="text/css">
      			{`
        .text-centered {
          text-align: center;
          margin-top: 20px;
          font-family: Garamond;
          color: white;
        }
        #startButton, #settings {
          margin-top: 100px;
          font-family: Garamond;
          text-align: center;

        }
        #surrounding {
        	border: 1px solid #000;
        	border-radius: 25px;
        	padding: 30px;
        	margin-top: 30px;
        	background-color: #ADD8E6;
        }
        a {
          color: rgb(0, 0, 0);
          text-decoration: none;
        }
        .spreadOut {
        	margin: 50px;
        }
        .spreadOutTiny {
        	margin: 20px;
        }
        span {
        	display: flex;
        }


      `}
        </style>
      	</head>
    <body>
    <Container fluid>
      <h1 class="text-centered">Tuscarora Student Answers</h1>
	</Container>
    </body>
   </div>    
      );
    }
  }
   class StartButton extends Component {
   	constructor(props) {
   		super(props);
   	}
   	render() {
   		return (
   		<div id="startButton">
   			<motion.div 
   					initial={{ opacity: 0, y:-50}} 
   					animate={{ opacity: 1, y: 0}} 
   					transition={{duration: 0.5}}
   					whileInView="visible" 
   					viewport={{ once: true }}

   					>
   			  <a className="btn btn-outline-primary btn-lg" href="/essay">Start</a>
   			 </motion.div>  			
   		</div>
   			)
   	}

   }

	return (
		<div id="loading">
		<body>
			<Snowfall />
		  <HeaderApp />
		  <StartButton />
		  <SettingModal />
		  <ModalButton />
		  <style type="text/css">
		  		{`
		  		body {
		  			background-color: black;
		  		}

		  		`}
			</style>
			</body>
		</div>
		)
}
	
export default LoadingScreen;