import React, {Component, useState, useRef, useEffect} from 'react';
import {Button, Col, Row, Navbar, Nav, ButtonGroup, Modal, Container, Form} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { motion } from "framer-motion"
import io from 'socket.io-client';
import Typewriter from "./typewriter";


export default function EssayHelper() {
  const [answer, setAnswer] = useState("Ask me a question")
  const inputText = useRef("");
  const socket = io("https://flaskbackendllm-1.onrender.com");

  
  const send = async () => {  
    await socket.emit("message", {"value": {inputText}})
    // gets result
    await fetch("https://flaskbackendllm-1.onrender.com")
    .then(response => response.json())  
    .then(json => {
        // Formats string
        let string = JSON.stringify(json["result"])
        setAnswer(string.substring(1, string.length-1))
    })
    
  }

  const handleChange = (event) => {
    inputText.current = event.target.value;
  }
  const handleKeyPress = (target) => {
    if (target.charCode==13) {
      send()
    }
  }

  class TextBlob extends Component {
    constructor(props) {  
      super(props);
    }
    render() {
      return (
      <div id="textPart">
        <Form onSubmit={(e) => {e.preventDefault()}}>
      <Form.Group className="mb-3" controlId="Essay">
        <Form.Control 
          type="textarea" 
          placeholder="Type here!" 
          value={inputText.current.value}
          onChange={handleChange}
          onKeyPress={handleKeyPress}
          onSubmit={(e) => {e.preventDefault()}}
          />
      </Form.Group>
      <Button variant="primary" id="button" onClick={send}>
      Submit
      </Button>
      </Form>
        </div>
      );
    }
  
  }
  class EssayHeader extends Component {
    constructor(props) {
      super(props);
    }
    render() {
      return (
      <div id="headerPart">
      	<h3 class="text-to-fade-in">Type in your prompt below!</h3>   
        </div>
      );
    }
  
  }
  class BackgroundPart extends Component {
  	constructor(props) {
  	  super(props);
  	  
  	}
  	render() {
  	  return (
  	  	<div id="essayBackground">
  	  	 <EssayHeader />
  	  	 <TextBlob />
         <p id="disclaimer"><strong>Disclaimer: </strong><i>ChatTSA is an experimental bot and can often bring up irrelevant data</i></p>
         <div id="bot_answer"><p color="white"><Typewriter text={answer} delay={20} /></p></div>	
         </div>

  	  )
  	}
  }

  
	return (
	<div id="essay">
	  <style type="text/css"> {`
	  body {
	  	height: 100%;
	  	margin: 0;
	  	background-color: #ADD8E6;
      background-repeat: no-repeat;
      background-size: 1800px 1200px
	  }
    input {
      margin-top: 50px;
    }

    .text-to-fade-in {
      text-align: center;
      margin-top: 60px;
      animation: typing 4s;
    }
    p {
      color: white;
    }
    #disclaimer {
      margin-top: 40px;
      margin-left: 150px;
      color: black;
    }

    #textPart {
      width: 80%;
      margin-left: 150px;
      margin-right: 100px;
      display: inline-block;
    }
    #button {
      display: inline-block;
    }
    #bot_answer {
      margin: 150px 100px;
      margin-top: 50px;
      padding: 30px;
      border: 1px solid #000;
      background-color: black;
    }
	  `}
	    </style>
	 <BackgroundPart />	
	  </div>
		)
}
